

.register-Content {
    z-index: 2;
    width: 45vw;
    flex-direction: column;
    justify-content:center;
    display: flex;
    box-shadow: #444444;
    position: relative;
    border-radius: 10px;
    height: 60vh;
    background-color: #444444;
    border: rgb(230, 226, 226) 1px solid;
  }
  .wrapper{
    width: 25%;
    padding: 20px;
    background-color: #444444;
    width:75%
  }
  .register-img {
    flex-wrap: wrap;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 50%;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-position: 80%;
    background-repeat: no-repeat;
    background-size: fill;
  }
  
  .register-img-login {
    flex-wrap: wrap;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 100%;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-position: 80%;
    background-repeat: no-repeat;
    background-size: fill;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    align-items: center;
    border: rgb(239, 234, 234) 1px solid;
    text-align: center;
    color: #444444;
    background-color: #444444;
  }
  
  .register-title {
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    color: rgb(8, 24, 243);
  }
  
  .register-title-span {
    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    font-style: italic;
  }
  
  .container-inputs {
   display: flex;
  }
  
  .register-inputs {
    width: 50%;
    display: flex; 
    justify-content: space-evenly;
  }
  
  .register-label {
    width: 30%;
    text-align: left;
    /* margin-left: 2em; */
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
  }
  
  .register-input {
    /* margin-left: 10px; */
    text-align: center;
    background-color: rgb(42, 42, 43);
  }
  
  .register-input-btn {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
  }
  
  .login-btn {
    margin-bottom: 1em;
  }
  
  .register-input-login {
    margin-bottom: 1em;
    border: 0.01rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
  }
  
  .register-login-btn {
    color: red;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
  }
  
  .register-success-content {
    /* z-index: 2; */
    width: 90vw;
    /* flex-direction: column; */
    /* justify-content: space-between; */
    display: flex;
    box-shadow: 0 5px 10px 0 rgba(83, 83, 83, 0.315), 0 7px 10px 0 rgb(97, 97, 97);
    position: relative;
    border-radius: 10px;
    height: 60vh;
    background-color: rgb(15, 13, 13);
    border: black 1px solid;
  }
  
  .success-img {
    flex-wrap: wrap;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 50%;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-position: 80%;
    background-repeat: no-repeat;
    background-size: fill;
  }
  
  .success-title-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }
  
  .close-btn {
    color: rgb(245, 240, 240);
  }
  
  .register-success-title {
    color: rgb(245, 240, 240);
    font-family: 'Poppins', sans-serif;
    margin-top: 55%;
    
  }
  .btn{

    background-color:blue;
}
  
  